<template>
  <div class="home">
    <article v-for="art in articles">
      <div class="item" @click="routerToArticle(art.id)">
        <div class="img" :style="{'background-image': 'url(' + art.banner +')'}"></div>
        <div class="info">
          <h3>{{ art.title}}</h3>
          <i>{{ art.date }}</i>
          <span>{{ art.content | subContent}}</span>
        </div>
      </div>
    </article>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'Home',
  mounted (){
    const api ='https://us-central1-expressapi-8c039.cloudfunctions.net/app/article';
    axios.get(api)
    .then(result=>{
      this.articles = result.data.data;
    })
  },
  data(){
    return{
      articles:null,
    }
  },
  methods: {
    routerToArticle:function(id){
      this.$router.push({name:"Article",params:{id:id}})
    }
  },
  filters:{
    subContent:(content)=>{
      return content.substring(0,150);
    }
  }
}
</script>
